import Dropzone from "dropzone";

class DropzoneWrapper {
    dropzone = null;
    dropzoneInput = null;
    previewTemplate = null;
    previewsContainer = null;
    dataAttributes = {};
    defaults = {
        url: '/dummy',
        thumbnailWidth: 50,
        thumbnailHeight: 50,
        parallelUploads: 20,
        autoQueue: true
    };

    constructor(element) {
        this.dropzone = $(element);
        this.dropzoneInput = this.dropzone.find('.dropzone__input');
        this.previewTemplate = this.dropzone.find('.dropzone__file-row');
        this.previewsContainer = this.dropzone.find('.dropzone__queue');
        this.dataAttributes = this.dropzone.data();

        this.defaults.previewTemplate = this.previewTemplate.get(0).outerHTML;
        this.defaults.previewsContainer = this.previewsContainer.get(0);
        this.defaults.clickable = this.dropzoneInput.get(0);

        // this.previewTemplate.remove();

        const myDropzone = new Dropzone(this.dropzoneInput.get(0), this.config());

        console.log(this.dropzoneInput.get(0).dropzone);
    }

    config() {
        return $.extend({}, this.defaults, this.dataAttributes);
    }
}

export default DropzoneWrapper;