export class Selectize {
    selectize         = null;
    dataAttributes = {};
    defaults       = {
        create: false,
        closeAfterSelect: true,
        hideSelected: true,
        maxItems: 1
    };

    constructor(element) {
        this.selectize = $(element);
        this.dataAttributes = this.selectize.data();
        this.selectize.selectize(this.config());
    }

    config() {
        return $.extend({}, this.defaults, this.dataAttributes);
    }
}
