import autosize from 'autosize';
import bsCustomFileInput from 'bs-custom-file-input';
import DropzoneWrapper from "./dropzone-wrapper";
import {includeSvgResource} from "./utils";
import {Selectize} from "./selectize";
import {Sticky} from "./sticky";
import {Touchspin} from "./touchspin";
import {Window} from "./window";

Dropzone.autoDiscover = false;

class App {
    /**
     * Body element
     *
     * @type {jQuery|HTMLElement}
     */
    body = $('body');

    /**
     * Windows service
     *
     * @type {Window}
     */
    window = new Window();


    constructor() {
        this.initAutosize();
        this.initDropzone();
        this.initFileInput();
        this.initPopovers();
        this.initSticky();
        this.initSvgResource();
        this.initSelectize();
        this.initTouchspin();
        this.initTooltips();

        this.handleHeaderToggleButtons();
    }

    initAutosize() {
        autosize($('[data-toggle="autosize"]'));
    }

    initDropzone() {
        $('[data-toggle="dropzone"]').each((key, item) => {
            new DropzoneWrapper(item);
        });
    }

    initFileInput() {
        bsCustomFileInput.init();
    }

    initPopovers() {
        $('[data-toggle="popover"]').popover();
    }

    initSelectize() {
        $('[data-toggle="selectize"]').each((key, item) => {
            new Selectize(item);
        });
    }

    initSticky() {
        $('[data-toggle="sticky"]').each((key, item) => {
            new Sticky(item);
        });
    }

    initSvgResource() {
        if (typeof TB.svgResource.path !== "undefined") {
            includeSvgResource(TB.svgResource.path, TB.svgResource.id, TB.svgResource.version);
        }
    }

    initTouchspin() {
        $('[data-toggle="touchspin"]').each((key, item) => {
            new Touchspin(item);
        });
    }

    /**
     * Trigger tooltips
     */
    initTooltips() {
        this.body.on('mouseenter', '[data-toggle="tooltip"]', function () {
            $(this).tooltip('show');
        });
    }

    /**
     * Header mobile nav button
     */
    handleHeaderToggleButtons() {
        this.body
            .on('click', '.btnNavToggle', (e) => {
                $('#headerCollapsable').toggleClass('show');
                $('#header').find('.navbar-toggler').toggleClass('collapsed');

                if ($('#headerCollapsable').is('.show')) {
                    $('html').addClass('mobileMenuShow');
                } else {
                    $('html').removeClass('mobileMenuShow');
                }
            })
            .on('click', '#header_collapse_button', function (e) {
                $('.tb__header')
                    .toggleClass('tb__header--collapsed');

                e.preventDefault();
            });
    }
}

$(function () {
    new App();
});

$('document').ready(function(){

  // Toggle side bar
  $('#toggle-sidebar , .mobile-case-toggle').on('click' , function(e){
    e.preventDefault();

    $(this).toggleClass('active');
    $('.case-sidebar').toggleClass('close-out');
    $('#content').toggleClass('wide');
  });

  $('.mobile-case-toggle').on('click' , function(){
    $(this).removeClass('active');
    $('.case-sidebar').toggleClass('open');
    $('html').toggleClass('overflow-y');
  });

  // Show link in table after checkbox checked
  $('.table-full-width input[type="checkbox"]').on('click' , function(){
    const count = $('.table-full-width input[type="checkbox"]:checked').length;
    if ( count != 0 ) {
        $('.active-item').removeClass('d-none');
    } else {
        $('.active-item').addClass('d-none');
    }

  });

  // Scroll table by click on button
  $('.scroll-right').on('click', function(e) {
    e.preventDefault();
    var leftPos = $(this).closest('div').next('.table-responsive').scrollLeft();
    console.log(leftPos);
    $(this).closest('div').next('.table-responsive').animate({
        scrollLeft: leftPos - 200
    }, 800);
  });

  $('.scroll-left').on('click', function(e) {
    e.preventDefault();
    var leftPos = $(this).closest('div').next('.table-responsive').scrollLeft();
    console.log(leftPos);
    $(this).closest('div').next('.table-responsive').animate({
        scrollLeft: leftPos + 200
    }, 800);
  });

  // Mobile nav
  $('.toggle-mobile').on('click' , function(){
    $(this).removeClass('active');
    $(this).toggleClass('open');
    $('.mobile-nav').toggleClass('open');
    $('html').toggleClass('overflow-y');
  });

  // Form builder. Open field setting
  $('.form-builder .field-option .field-setting').on('click' , function(e){
    e.preventDefault();

    // Hide the form elements
    $('.form-builder-wrap #accordion .card-body').closest('.show').find('.form-builder-element-wrap').toggleClass('d-flex d-none');
    // Show field setting
    $('.form-builder-wrap #accordion .card-body').closest('.show').find('.form-builder-element-setting').toggleClass('d-none d-block');
    // Show button for save setting
    $('.form-builder-wrap').find('.field-setting-save').toggleClass('d-none d-inline-flex');

    $(this).toggleClass('open');
  });

});
