import {convertToObject} from "./utils";
import {Window} from "./window";

export class Sticky {
  sticky         = null;
  dataAttributes = {};
  defaults       = {
    sticky_class: 'sticky-active'
  };

  constructor(element) {
    this.sticky = $(element);
    this.dataAttributes = this.sticky.data();
    this.sticky.stick_in_parent(this.config());

    this.initEvents();
  }

  config() {
    return $.extend({}, this.defaults, this.dataAttributes);
  }

  initEvents() {
    this.addClasses();
    this.removeClasses();
    this.removeInnerClasses();
    this.swapInnerClasses();
    this.disableUnderMethod();
    this.disableOverMethod();
  }

  reInitSticky() {
      this.sticky.stick_in_parent(this.config());
      this.addClasses();
      this.removeClasses();
      this.removeInnerClasses();
      this.swapInnerClasses();
  }

  addClasses() {
    if (this.dataAttributes.hasOwnProperty('addClass')) {
      this.sticky
        .on('sticky_kit:stick', (e) => {
          this.sticky.addClass(this.dataAttributes.addClass);
        })
        .on('sticky_kit:unstick', (e) => {
          this.sticky.removeClass(this.dataAttributes.addClass);
        });
    }
  }

  removeClasses() {
    if (this.dataAttributes.hasOwnProperty('removeClass')) {
      this.sticky
        .on('sticky_kit:stick', (e) => {
          this.sticky.removeClass(this.dataAttributes.removeClass);
        })
        .on('sticky_kit:unstick', (e) => {
          this.sticky.addClass(this.dataAttributes.removeClass);
        });
    }
  }

  removeInnerClasses() {
    if (this.dataAttributes.hasOwnProperty('removeInnerClass') && this.dataAttributes.removeInnerClass) {
      var classes = this.dataAttributes.removeInnerClass.split(' ').map((value) => {
        return '.' + value;
      });

      this.sticky.on('sticky_kit:stick', (e) => {
        var $elements = this.sticky.find(classes.join(', '));

        $elements.each((i, elem) => {
          var $el = $(elem),
            class_list = '';

          classes.forEach((value) => {
            class_list += $el.is(value) ? value.replace('.', '') + ' ' : '';
          });

          $el
            .data('removed-classes', class_list.trim())
            .addClass('sticky-modified')
            .removeClass(this.dataAttributes.removeInnerClass);
        });
      });

      this.sticky.on('sticky_kit:unstick', (e) => {
        this.sticky.find('.sticky-modified').each((i, elem) => {
            $(elem).addClass($(elem).data('removed-classes'));
        });
      });
    }
  }

  swapInnerClasses() {
    if (this.dataAttributes.hasOwnProperty('swapInnerClass') && this.dataAttributes.swapInnerClass) {
      var rules = convertToObject(this.dataAttributes.swapInnerClass),
        classes = Object.keys(rules).map((value) => {
          return '.' + value;
        }),
        $elements = this.sticky.find(classes.join(', '));

      this.sticky.on('sticky_kit:stick', (e) => {
        $elements.each((i, elem) => {
          var $el = $(elem);

          Object.keys(rules).forEach((key) => {
            if ($el.hasClass(key)) {
              $el.removeClass(key);
              $el.addClass(rules[key]);
            }
          });

        });
      });

      this.sticky.on('sticky_kit:unstick', (e) => {
        $elements.each((i, elem) => {
          var $el = $(elem);

          Object.keys(rules).forEach((key) => {
            if ($el.hasClass(rules[key])) {
              $el.removeClass(rules[key]);
              $el.addClass(key);
            }
          });
        });
      });
    }
  }

  disableUnderMethod() {
      if (this.dataAttributes.hasOwnProperty('disableUnder') && this.dataAttributes.disableUnder) {
          const disable_under = this.dataAttributes.disableUnder;

          if ((new Window()).width < disable_under) {
              this.sticky.trigger('sticky_kit:detach');
          }

          window.addEventListener(window.hasOwnProperty('onorientationchange') ? 'orientationchange' : 'resize', () => {
              if ((new Window()).width < disable_under) {
                  this.sticky.trigger('sticky_kit:detach')
              } else {
                  this.reInitSticky();
              }
          });
      }
  }

  disableOverMethod() {
      if (this.dataAttributes.hasOwnProperty('disableOver') && this.dataAttributes.disableOver) {
          const disable_over = this.dataAttributes.disableOver;

          if ((new Window()).width > disable_over) {
              this.sticky.trigger('sticky_kit:detach');
          }

          window.addEventListener(window.hasOwnProperty('onorientationchange') ? 'orientationchange' : 'resize', () => {
              if ((new Window()).width > disable_over) {
                  this.sticky.trigger('sticky_kit:detach')
              } else {
                  this.reInitSticky();
              }
          });
      }
  }
}