export class Touchspin {
    touchspin         = null;
    dataAttributes = {};
    defaults       = {
        buttondown_class:  'btn btn-white',
        buttonup_class:    'btn btn-white',
        verticalupclass:   'tb__icon--chevron-top',
        verticaldownclass: 'tb__icon--chevron-bottom'
    };

    constructor(element) {
        this.touchspin = $(element);
        this.dataAttributes = this.touchspin.data();
        this.touchspin.TouchSpin(this.config());

        this.touchspin.closest('.input-group').toggleClass('w-auto flex-grow-0 flex-shrink-0 flex-basis-auto', this.touchspin.is('[size]'));
    }

    config() {
        return $.extend({}, this.defaults, this.dataAttributes);
    }
}
